import React from 'react';
import styled from 'styled-components';
import { bindActionCreators } from 'redux';
import { gameActions } from './../../actions';
import { connect } from 'react-redux';
// components
import AnnouncementBackdrop from './AnnouncementBackdrop';
import AnnouncementMessage from './AnnouncementMessage';
import AnnouncementModal from './AnnouncementModal';

const Button = styled.button`
  width: 100px;
  height: 30px;
  outline: none;
  border: 2px solid white;
  background: transparent;
  color: white;
  &:hover {
    border: none;
    background: white;
    color: black;
  }
`;

const Announcement = ({ actions }) => {
  return (
    <AnnouncementBackdrop>
      <AnnouncementModal>
        <AnnouncementMessage>
          message
          <Button onClick={actions.startGame}>Play Again!</Button>
        </AnnouncementMessage>
      </AnnouncementModal>
    </AnnouncementBackdrop>
  );
};

export default connect(
  null,
  dispatch => ({ actions: bindActionCreators(gameActions, dispatch) })
)(Announcement);
